#IDsectionAboutMe {
    position: relative;
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: start;
    background-color: var(--black);
                
    background-size: 50px 50px;
    background-position: center;
    overflow: hidden;

    background-image: url('../2_assets/images/walltexture.jpg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

#IdallContentAboutMe {
    z-index: 25;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: start;
    overflow: auto;
}

.titleAboutMe {
    position: relative;
    margin-top: 30vh;
    font-family: titleone;
    font-size: 6vh;
    color: var(--red);

    color: #d0fcff;
        text-shadow:
            0 0 0.5vh var(--aqua),
            0 0 1vh var(--aqua),
            0 0 1.5vh var(--aqua),
            0 0 2vh var(--aqua),
            0 0 2.5vh var(--aqua),
            0 0 3vh var(--aqua),
            0 0 4vh var(--aqua);

    border-radius: 1vh;
    padding-left: 2vh;
    padding-right: 2vh;
    padding-top: 2.2vh;
    padding-bottom: 1vh;
    border: 0.4vh solid #ffffff;
    box-shadow:
        0 0 1vh var(--white),
        0 0 2vh var(--white),
        0 0 3vh var(--white),
        inset 0 0 1vh var(--white),
        inset 0 0 2vh var(--white);
}

.text-frame {
    position: relative;
    width: 60%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 10vh;
    border-radius: 3vh;
    margin-top: 10vh;
    margin-bottom: 10vh;
}

.text-frame-bkg {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: var(--black);
    opacity: 85%;
    border-radius: 2vh;
}

#IDsectionAboutMe div p {
    z-index: 30;
    max-width: 90%;
    margin-top: 5vh;
    margin-bottom: 5vh;
    position: relative;
    font-family: name;
    font-size: 2.5vh;
    line-height: 1.7;
    color: #bebebe;
    text-indent: 4vh;
    text-align: justify;
    hyphens: auto;
}

@media (max-width: 899px) {

    .titleAboutMe {
        margin-top: 25vh;
        font-size: 3.6vh;
    }

    .splashWhite {
        z-index: 22;
        position: absolute;
        height: 40vh;
        top: -10vh;
        right: -10vh;
    }
    
    .splashAqua {
        z-index: 22;
        position: absolute;
        height: 40vh;
        bottom: -10vh;
        left: -5vh;
    }

    .text-frame {
        position: relative;
        width: 90%;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 10vh;
        margin-bottom: 10vh;
        border-radius: 3vh;
    }

    #IDsectionAboutMe div p {
        font-size: 2vh;
        padding: 0.5vh;
    }

}