#IdContactPageAll {
    position: relative;
    width: 100%;
    min-height: 100vh;
    background: linear-gradient(to bottom, var(--purpleBlack), var(--purple));
}

.contentAll {
    position: absolute;
    top: 30vh;
    z-index: 60;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.titleContact {
    font-family: titleone;
    font-size: 6vh;
    color: var(--red);
    margin-bottom: 8vh;
    color: #c3fffe;
        text-shadow:
            0 0 0.5vh var(--aqua),
            0 0 1vh var(--aqua),
            0 0 1.5vh var(--aqua),
            0 0 2vh var(--aqua),
            0 0 2.5vh var(--aqua),
            0 0 3vh var(--aqua),
            0 0 4vh var(--aqua);

    border-radius: 1vh;
    padding-left: 2vh;
    padding-right: 2vh;
    padding-top: 2.2vh;
    padding-bottom: 1vh;
    border: 0.4vh solid #ffc6c6;
    box-shadow:
        0 0 1vh var(--red),
        0 0 2vh var(--red),
        0 0 3vh var(--red),
        inset 0 0 1vh var(--red),
        inset 0 0 2vh var(--red);
}

.textContact {
    font-family: name;
    color: var(--white);
    font-size: 2.3vh;
    line-height: 1.7;
    text-indent: 2vh;
    text-align: justify;
    margin-bottom: 15vh;

    width: 90ch;
    max-width: 90%;
    margin-left: auto;
    margin-right: auto;
}

.textContact a {
    text-decoration: none;
    color: var(--red);
}

.sociaMediaContact {
    display: flex;
    margin-top: 2vh;
    align-items: center;
    justify-content: center;
    gap: 3vh;
}

.sociaMediaContact button {
    position: relative;
    width: 6vh;
    height: 6vh;
    cursor: pointer;display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 100%;

    border: 0.4vh solid #ffffff;
    box-shadow:
        0 0 1vh var(--yellow),
        0 0 1.5vh var(--yellow),
        0 0 1.5vh var(--yellow),
        inset 0 0 0.3vh var(--yellow),
        inset 0 0 0.5vh var(--yellow),
        inset 0 0 1vh var(--yellow);
}

.sociaMediaContact button:hover {
    border: 0.4vh solid #ffffff;
    box-shadow:
        0 0 1vh var(--red),
        0 0 1.5vh var(--red),
        0 0 1.6vh var(--red),
        inset 0 0 1vh var(--red),
        inset 0 0 1.2vh var(--red),
        inset 0 0 1.3vh var(--red);
}

.sociaMediaContact img {
    position: absolute;
    width: 55%;
    height: 55%;
}

.wrapTextTop {
    width: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-bottom: 3vh;
}

.socialMediaTxt {
    z-index: 9;
    font-family: name;
    font-size: 5vh;
    text-align: center;
    color: transparent;
    -webkit-text-stroke: 0.1vh #ffe9d4;
    animation: glowMedia 2s ease-in-out infinite;
}

@keyframes glowMedia {

    0%{
        text-shadow:
            0 0 0.5vh var(--orange),
            0 0 1vh var(--orange),
            0 0 1.5vh var(--orange),
            0 0 2vh var(--orange);
    }

    50% {
        text-shadow:
            0 0 0.5vh var(--orange),
            0 0 1vh var(--orange),
            0 0 1.5vh var(--orange),
            0 0 2vh var(--orange),
            0 0 2.5vh var(--orange),
            0 0 3vh var(--orange),
            0 0 3.5vh var(--orange),
            0 0 4vh var(--orange),
            0 0 4.5vh var(--orange),
            0 0 5vh var(--orange);
    }

    100%{
        text-shadow:
            0 0 0.5vh var(--orange),
            0 0 1vh var(--orange),
            0 0 1.5vh var(--orange),
            0 0 2vh var(--orange);
    }
    
}

.findMeContact {
    position: absolute;
        bottom: -2.3vh;
        z-index: 9;
        font-family: 'cursive', sans-serif;
        font-size: 2.5vh;
        color: var(--aqua);
        text-shadow:
            0 0 0.5vh var(--aqua),
            0 0 1vh var(--aqua),
            0 0 2vh var(--aqua),
            0 0 3vh var(--aqua),
            0 0 4vh var(--aqua);
    }

.stripesContact {
    position: absolute;
    z-index: 50;
    width: 100%;
    min-height: 100vh;
    background: linear-gradient(
        90deg, 
        var(--purple) 25%, 
        transparent 25%, 
        transparent 50%, 
        var(--purple) 50%, 
        var(--purple) 75%, 
        transparent 75%, 
        transparent 100%
    );
    background-size: 50px 100%;
    opacity: 40%;
}

@media (max-width: 899px) {

    .contentAll {
        top: 25vh;
    }

    .titleContact {
        font-size: 3.6vh;
    }

    .textContact {
        width: 85%;
        font-size: 1.7vh;
    }
    
    .socialMediaTxt {
        font-size: 4vh;
    }

    .findMeContact{
        font-size: 2vh;
        margin-bottom: 1vh;
    }

    .wrapTextTop {
        margin-bottom: 2vh;
    }

    .sociaMediaContact button {
        width: 4vh;
        height: 4vh;
        border: 0.2vh solid #ffffff;
    }

    .sociaMediaContact button:hover {
        border: 0.2vh solid #ffffff;
    }

}